body {
  background-color: black;
  color: white;
  margin: 0;
  background-image: url(https://picsum.photos/1200/900/?image=893);
  background-size: cover;
}
.App {
  font-family: sans-serif;
  height: 100vh;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  --delay: 500ms;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.App.dragging {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
.boards {
  position: relative;
  height: 100%;
  width: 25%;
  background-color: rgba(0, 0, 0, 0.5);
  border: solid 1px rgba(255, 255, 255, 0.5);
  overflow-y: auto;
  padding: 2px;
}
.boards p {
  border-radius: 10px;
  padding: 3px;
  margin: 1px;
}
.boards p.selected {
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.5);
}
.gameboard-holder {
  width: 75%;
  max-width: 75vh;
  overflow: hidden;
}
.gameboard {
  width: 100%;
  border: solid 1px white;
  position: relative;
}
.game-grid {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: rgb(0, 0, 0);
}
.game-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
}
.game-cell {
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  -ms-flex: 1 1;
      flex: 1 1;
  border-radius: 10%;
  background-image: -webkit-gradient(
    linear,
    left bottom, left top,
    from(hsla(0, 0%, 0%, 0)),
    to(hsla(0, 0%, 0%, 0.5))
  );
  background-image: -webkit-linear-gradient(
    bottom,
    hsla(0, 0%, 0%, 0) 0%,
    hsla(0, 0%, 0%, 0.5) 100%
  );
  background-image: -o-linear-gradient(
    bottom,
    hsla(0, 0%, 0%, 0) 0%,
    hsla(0, 0%, 0%, 0.5) 100%
  );
  background-image: linear-gradient(
    to top,
    hsla(0, 0%, 0%, 0) 0%,
    hsla(0, 0%, 0%, 0.5) 100%
  );
  border: solid 1px hsla(0, 67%, 40%);
  -webkit-transition: background-color var(--delay) ease, border-color var(--delay) ease;
  -o-transition: background-color var(--delay) ease, border-color var(--delay) ease;
  transition: background-color var(--delay) ease, border-color var(--delay) ease;
  /* border-right: solid 1px rgba(255, 255, 255, 0.5);
  border-bottom: solid 1px rgba(255, 255, 255, 0.5); */
}
.game-row:last-child .game-cell {
  border-bottom: none;
}
.game-cell:last-child {
  border-right: none;
}

.game-cell.transparent {
  background: transparent;
  border-color: transparent;
}
.game-cell.star:after {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  background-image: url(./img/star.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  -webkit-filter: drop-shadow(0.2vh 0.2vh 1px rgba(255, 81, 0, 0.5));
          filter: drop-shadow(0.2vh 0.2vh 1px rgba(255, 81, 0, 0.5));
  -webkit-transition: opacity var(--delay) ease;
  -o-transition: opacity var(--delay) ease;
  transition: opacity var(--delay) ease;
  opacity: 1;
}
.game-cell.star.gone:after {
  opacity: 0;
}
.spacer {
  padding-top: 75%;
}
.game-ship-holder {
  pointer-events: none;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-transition: -webkit-transform var(--delay) linear;
  transition: -webkit-transform var(--delay) linear;
  -o-transition: transform var(--delay) linear;
  transition: transform var(--delay) linear;
  transition: transform var(--delay) linear, -webkit-transform var(--delay) linear;
}
.game-ship {
  width: 6.25%;
  -webkit-transition: -webkit-transform var(--delay) ease;
  transition: -webkit-transform var(--delay) ease;
  -o-transition: transform var(--delay) ease;
  transition: transform var(--delay) ease;
  transition: transform var(--delay) ease, -webkit-transform var(--delay) ease;
}
.game-controls {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.player-controls {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.function-holder {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.function-holder img {
  width: 30px;
  height: 30px;
}
.function-area {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  max-width: 220px;
}
.function-block {
  width: 30px;
  height: 30px;
  margin: 5px;
  background-color: rgba(0, 0, 0, 0.2);
  border: dashed 1px rgba(255, 255, 255, 0.3);
  border-radius: 20%;
  -webkit-transition: background-color 0.2s ease;
  -o-transition: background-color 0.2s ease;
  transition: background-color 0.2s ease;
}
.game-controls.dragging .function-block:hover {
  background-color: rgba(255, 255, 255, 0.5);
}
.commands-area {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  border: solid 1px rgba(255, 255, 255, 0.5);
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 10px;
}
.commands-area.dragging .command {
  pointer-events: none;
}
.divider {
  height: 10px;
}
.command {
  cursor: -webkit-grab;
  cursor: grab;
  width: 30px;
  height: 30px;
  margin: 5px;
  border-radius: 20%;
  background-position: center;
  background-repeat: no-repeat;
  background-color: hsla(0, 0%, 70%, 1);
  border: solid 1px hsla(0, 0%, 40%);
  background-size: 70%, 100%;
  background-image: url(),
    -webkit-gradient(linear, left bottom, left top, from(hsla(0, 0%, 0%, 0)), to(hsla(0, 0%, 0%, 0.5)));
  background-image: url(),
    -webkit-linear-gradient(bottom, hsla(0, 0%, 0%, 0) 0%, hsla(0, 0%, 0%, 0.5) 100%);
  background-image: url(),
    -o-linear-gradient(bottom, hsla(0, 0%, 0%, 0) 0%, hsla(0, 0%, 0%, 0.5) 100%);
  background-image: url(),
    linear-gradient(to top, hsla(0, 0%, 0%, 0) 0%, hsla(0, 0%, 0%, 0.5) 100%);
}
.command:active {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
